div.csesearch .form-group {
    display: inline-block;
}
div.csesearch div.results{
    display:inline-block;
    margin-bottom:1em;
}
div.csesearch div.listingbar{
    clear:both;
    font-size:1em;
}
div.csesearch ul.pagination{
    list-style-type:none;
    padding-left:0;
}
div.csesearch ul.pagination li{
    display:inline-block;
    font-size:1.3rem;
}
div.csesearch div.results div.row{
    margin-bottom:1em;
}

div.csesearch span.results-url {
    padding-top: .3em;
    display: inherit;
    color:#008000 ;
}

